.main {
  max-width: 1220px;
  padding: 0 20px;
  margin: 0 auto
}

.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.header__row {
  display: flex;
  margin-bottom: 15px;
}

.game__grid {
  display: grid;
  grid-template-columns: repeat(3, 150px);
}

.game__border {
  height: 150px;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
}

.main__row {
  display: flex;
  justify-content: space-evenly; 
}

.messages {
  background-color: lightgray;
  width: 350px;
  height: 470px;
  padding: 7px;
}

.header__row p {
  margin-right: 50px;
  font-size: 17px;
  font-weight: 600;
}

.right-bottom-border {
  border-right: 1px solid;
  border-bottom: 1px solid;
}

.bottom-border {
  border-bottom: 1px solid;
}

.right-border {
  border-right: 1px solid;
}

@media (max-width: 992px) {
  .game__grid {
    grid-template-columns: repeat(3, 120px);
  }
  
  .game__border {
    height: 120px;
  }

  .messages {
    width: 300px;
    height: 370px;
  }
}

@media (max-width: 767px) {
  .main__row {
    flex-direction: column;
    justify-content: center;
  }

  .flex-center {
    display: flex;
    justify-content: center;
  }

  .chat {
    margin-top: 20px;
    margin-bottom: 30px;
  }

  .messages {
    width: 100%;
    height: 400px;
  }
}

@media (max-width: 450px) {
  .game__grid {
    grid-template-columns: repeat(3, 105px);
  }
    
  .game__border {
    height: 105px;
  }
}

.chat-form {
  display: flex;
}

.chat-form textarea {
  width: calc(100% - 89px);
  height: 54px;
  margin: 0;
  resize: none;
}

.chat-form button {
  height: 60px;
  margin: 0;
}

.messages p {
  margin: 0;
}

.login-message {
  font-size: 17px;
  font-weight: 600;

}

.text-message {
  position: relative;
  top: -3px;
}

.my-message {
  text-align: right;
}

.messages > p {
  margin-top: 7px;
}